.content{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction:column;
        flex-direction:column;
    -ms-flex-align:baseline;
        align-items:baseline;
    width:80%;
    margin:auto; 
}

.align{
  text-align:left;  
  width:100%;
}
input{
    width: 100%;
    height: 40px;
    font-size: 120%;
    border: none;
    border-bottom: 1px solid #d8d8d8;
    outline:none;
}
.margin-bottom{
    margin-bottom:0px;
}
textarea{
    border-style: none;
    overflow: auto;
    outline: none;
    resize: none;
    border: 1px solid #d8d8d8;
    outline:none;
    width:100%;
    padding: 2%;
    font-size: 100%;
    -webkit-box-shadow:1px 1px 1px 0px #d8d8d8;
            box-shadow:1px 1px 1px 0px #d8d8d8; 
}
.input-btns{
    padding: 3%;
}
#save-btn{
    margin-right: 30px;
}



